@import url(https://fonts.googleapis.com/css?family=VT323);

.sudoku {
    margin-left: 21vw;
    margin-top: 14vh;
    font-size: 5vw;
    font-family: 'Courier New', monospace;
}

.container {
    display: table;
    margin-left: 16vw;
    width: 80%;
    text-align: right;
    margin-bottom: -80px;
}

.sudoku-desc {
    display: table-cell;
    font-family: 'Courier New', monospace;
    font-size: 18px;
    width: 28vw;
    text-align: justify;
}

.generate-grid {
    display: table-cell;
    white-space:pre;
    font-size: 35px;
}

.solve-grid {
    display: table-cell;
    white-space:pre;
    font-size: 35px;
}

/* Button design taken from Retro Buttons Website by b.Stayles */
.sudoku-button {
	background: #7C7C7C;
	border-bottom: 6px inset rgba(0,0,0,.5);
	border-left: 6px inset rgba(0,0,0,.5);
	border-right: 6px inset rgba(255,255,255,.5);
	border-top: 6px inset rgba(255,255,255,.5);
	color: white;
	cursor: pointer;
	font-size: 2.2rem;
	margin: 1rem;
	min-width: 200px;
	padding: .5rem;
	text-transform: uppercase;
	width: auto;
    font-family: 'VT323', monospace ;
}

.solve-button:hover {
    background: #BCBCBC;
}

.easy-button {
	background: #0000BC;
    margin-left: 49%;
}

.easy-button:hover {
    background: #0000FC;
}

.medium-button {
	background: #005800;
}

.medium-button:hover {
    background: #006800;
}

.hard-button {
    background: #881400;
}

.hard-button:hover {
    background: #A81000;
}

@media (max-width: 800px) {
    .sudoku { 
        margin-left: 43vw;
        font-size: 10vw;
    }

    .container {
        margin-left: 35vw;
        margin-bottom: 0vw;
    }

    .sudoku-desc {
        padding-left: 0vw;
        padding-right: 25vw;
    }
}