/* Design taken from CSS Scan by Michael McMillan*/

.page-link {
  font-family: "Open Sans", sans-serif;
  font-size: 1vw;
  letter-spacing: .1vw;
  text-decoration: none;
  text-transform: uppercase;
  color: #000;
  cursor: pointer;
  border: .25vw solid;
  margin-right: .8vw;
  box-shadow: .01vw .01vw 0vw 0vw, .02vw .02vw 0vw 0vw, .1vw .1vw 0vw 0vw, .2vw .2vw 0vw 0vw, .3vw .3vw 0vw 0vw;
  position: relative;
  margin-bottom: .8vh;
  -ms-user-select: none;
      user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.page-link:active {
  box-shadow: 0vw 0vw 0vw 0vw;
  top: .3vw;
  left: .3vw;
}

@media (min-width: 800px) {
  .page-link {
    padding: .4vh .5vw;
  }
}

@media (max-width: 800px) {
  .page-link {
    font-size: 2vw;
  }
}
.menu-bar {
    padding-top: max(3vh, env(safe-area-inset-top));
    padding-bottom: max(3vh, env(safe-area-inset-bottom));
    padding-left: max(3vh, env(safe-area-inset-left));
    padding-right: max(3vh, env(safe-area-inset-right));
    justify-content: space-between;
    background-color: #474b4f;
    top: 0px;
    left: 0px;
    width: 100%;
    overflow: hidden;
    position: fixed;
    z-index: 400;
    display: flex;
}

.my-name {
    position: absolute;
    right: max(8vw, env(safe-area-inset-right) + 8vw);
    top: 3vh;
    font-family: "Open Sans", sans-serif;
    font-size: 1.8vw;
    letter-spacing: .1vw;
    text-transform: uppercase;
    color: white;
    -webkit-text-stroke: .05vw black;
}

.version {
    font-size: .7vw;
    position: absolute;
    color: gray;
    top: 60%;
    z-index: 401;
    right: max(8vw, env(safe-area-inset-right) + 8vw);
}

@media (max-width: 1400px) {
    .version {
        font-size: 1vw;
    }
}

@media (max-width: 1200px) {
    .version {
        font-size: 1.3vw;
        right: max(8.5vw , env(safe-area-inset-right) + 8.5vw);
    }
}

@media (max-width: 1000px) {
    .version {
        font-size: 1.6vw;
        right: max(9vw, env(safe-area-inset-right) + 9vw);
    }
}

@media (max-width: 800px) {
    .menu-bar {
        padding-top: max(1vh, env(safe-area-inset-top));
        padding-bottom: max(1vh, env(safe-area-inset-bottom));
        padding-left: max(1vh, env(safe-area-inset-left));
        padding-right: max(1vh, env(safe-area-inset-right));
    }

    .my-name {
        position: relative;
        right: 0;
        top: .5vh;
        padding-right: max(3vw, env(safe-area-inset-right));
        font-size: 2.5vw;
    }

    .version {
        display: none;
    }
}
.side-bar {
    height: 100%;
    width: 14vw;
    position: fixed;
    z-index: 200;
    top: 0;
    left: 0;
    padding-top: 10vh;
    padding-left: env(safe-area-inset-left);
    background-color: rgb(32, 32, 32);
    overflow-x: hidden;
}

/* Design taken from CSS Scan by Tailwind*/

.buttons {
  background-color: rgb(32, 32, 32);
  border: 1px solid transparent;
  border-radius: .75rem;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  flex: 0 0 auto;
  font-family: "Inter var",ui-sans-serif,system-ui,-apple-system,system-ui,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  font-size: 1.125rem;
  font-weight: 600;
  line-height: 1.5rem;
  padding: .75rem 0rem;
  text-align: center;
  -webkit-text-decoration: none #6B7280 solid;
          text-decoration: none #6B7280 solid;
  text-decoration-thickness: auto;
  transition-duration: .2s;
  transition-property: background-color,border-color,color,fill,stroke;
  transition-timing-function: cubic-bezier(.4, 0, 0.2, 1);
  -ms-user-select: none;
      user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: auto;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
}

.buttons:hover {
    color: #474b4f;
}

.buttons:focus {
  box-shadow: none;
  outline: 2px solid transparent;
  outline-offset: 2px;
}

@media (min-width: 800px) {
  .buttons {
    padding: .75rem 1rem;
  }
}

@media (max-width: 800px) {
  .side-bar {
    width: 27vw;
  }
}

@media (max-height: 500px) {
  .side-bar {
    width: 17vw;
    padding-left: 2vw;
  }
}



.image {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20vh;
    margin-bottom: 10vh;
    max-width: 50%;
    height: 55vh;
    border-radius: max(1vw, 1vh);
    -webkit-animation-name: fade;
            animation-name: fade;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    object-fit: contain;
    position: relative;
    z-index: 200;
}

@-webkit-keyframes fade {
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes fade {
    from {opacity: 0;}
    to {opacity: 1;}
}

